#editor {
  border: 1px solid #86786e;
  border-radius: 0.25rem;
  padding: 1rem 4rem 2rem 4rem;
  margin-top: 2rem;

  display: flex;
  flex-direction: column;
  align-items: center;

}
  h1 {
    margin-bottom: 1.5rem;
    font-size: 80px;

  }

  h2 {
    margin-bottom: 1rem;
    font-size: 40px;
  }



  .button {
    font-size: 1.25rem;
    background-color: #86786e;
    color: #c1bdc6;
    padding: 1rem 3rem;
    border-radius: 0.25rem;
    text-transform: uppercase;
    border: 1px solid transparent;
    transition: background-color 0.2s ease-in-out;
    margin-bottom: 2rem;
    
  }
    .button:hover {
      cursor: pointer;
      background-color:#4c442c;
      border-color: #44646c;
    }
  





  #options {
    display: flex;
    margin-bottom: 3rem;
    justify-content: center;
  }
    .option {
      display: flex;
      flex-direction: column;
    }

    .panelInput {
      height: 5rem;
      width: 5rem;
      font-size: 2rem;
      text-align: center;
      background-color: black;
      color: white;
      border: 1px solid grey;
      border-radius: 0.25rem;
      margin: 0 1rem 0.5rem 1rem;
      padding-left: 1rem;

    }
      .panelInput focus {
        outline: none !important;
        border-color: #86786e;
        box-shadow: 0 0 0.5rem grey;
      }
    
  



  .circle-picker {
    margin-bottom: 1.5rem !important;
  }

